/* UserProfile.css */

/* Common styles for the user profile container */
.user-profile-container {
  display: flex;
  justify-content: space-between;
  margin: 20px;
  padding: 20px;
  background-color: #f5f5f5;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.user-profile-container h2 {
  font-size: 24px;
  margin-bottom: 20px;
  color: #333;
}

.left-side {
  flex: 2;
  display: flex;
  flex-direction: column;
}

.right-side {
  flex: 1;
}

/* Expense Form styling */
.expense-form {
  width: 100%;
  max-width: 350px;
  margin-top: 0px;
  margin-left: 0px;
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.expense-form h3 {
  font-size: 20px;
  margin-bottom: 15px;
  color: #555;
}

.expense-form label {
  display: block;
  margin-bottom: 8px;
  color: #333;
}

.expense-form select,
.expense-form input {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 4px;
  transition: border-color 0.3s ease-in-out;
}

.expense-form select:focus,
.expense-form input:focus {
  border-color: #4caf50;
}

.expense-form button {
  width: 100%;
  padding: 12px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.expense-form button:hover {
  background-color: #45a049;
}

.expense-form .success-message {
  color: #4caf50; /* Green color, adjust as needed */
  margin-top: 10px; /* Adjust spacing from the form */
  font-weight: bold;
}

/* Filter Expenses Form styling */
.left-side form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 20px;
  width: 250px;
  padding: 15px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.left-side form label {
  color: #555;
  font-weight: bold;
  margin-bottom: 8px;
}

.left-side form select,
.left-side form input {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 4px;
  transition: border-color 0.3s ease-in-out;
}

.left-side form select:focus,
.left-side form input:focus {
  border-color: #4caf50;
}

.left-side form button {
  width: 100%;
  padding: 12px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.left-side form button:hover {
  background-color: #45a049;
}

/* Filtered Expenses Section styling */
.left-side table {
  width: 50%;
  border-collapse: collapse;
  margin-top: 20px;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(175, 98, 98, 0.1);
  border-radius: 8px;
  overflow: hidden;
}

.left-side table th,
.left-side table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

.left-side table th {
  background-color: #4caf50;
  color: white;
}

.left-side table tbody tr:hover {
  background-color: #f5f5f5;
}

/* Updated styling for Expense Categories Percentage pie-chart */
.left-side .chart-container {
  max-width: 1%; /* Adjust the max-width to 10% of the page width */
  margin-top: 200px;
  background-color: #a9a4a4;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  overflow: hidden;
}

.left-side .react-chartjs-2 {
  max-width: 10%; /* Adjust the max-width to 100% within the chart container */
  height: auto; /* Allow the height to adjust proportionally */
}


