/* Register.css */

.signup-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 8px;
}

.signup-form {
  width: 100%;
  margin-top: 3px;
}

.signup-form .MuiTextField-root {
  margin-bottom: 16px;
}

.signup-form .MuiButton-root {
  margin-top: 16px;
}

/* Additional styling for error messages */
.error-message {
  color: red; /* or your preferred error color */
  margin-top: 8px;
}

/* Additional styling for success message */
.success-message {
  color: green; /* or your preferred success color */
  margin-top: 16px;
  font-weight: bold;
}
