/* src/components/Home/Home.css */

.home-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
  }
  
  
  .app-header a {
    color: white;
    text-decoration: none;
    margin: 0 10px;
  }
  
  .intro-section {
    text-align: center;
    margin: 40px 0;
    background-color: #b8cbc8; /* Light Grayish Background */
    padding: 20px;
  }
  
  .intro-section h1 {
    font-size: 2.5em;
    margin-bottom: 10px;
  }
  
  .intro-section p {
    text-align: center;
    font-size: 1.2em;
    color: #666;
  }
  
  .intro-section button {
    background-color: #117671;
    color: white;
    padding: 10px 20px;
    font-size: 1.2em;
    border: none;
    cursor: pointer;
  }
  
  .expense-section {
    background-color:#3c3844;
    color: white;
    margin: 40px 0;
    padding: 20px;
  }
  
  .expense-section h2 {
    text-align: center;
    color: white;
    font-size: 2em;
    margin-bottom: 10px;
  }

  .expense-section p {
    text-align: center;
    font-size: 1.2em;
    color: white;
  }
  
  .expense-section h3 {
    font-size: 1.5em;
    color: white;
    margin-bottom: 10px;
  }

.expense-section h3 {
    font-size: 1.5em;
    margin-bottom: 10px;
    text-align: center;
  }

.key-features {
    display: flex;
    justify-content: center;
    flex-wrap: wrap; /* Allow wrapping to the next line if needed */
  }
  
  .key-feature-box {
    background-color: #656073;
    padding: 20px;
    margin: 20px; /* Add margin for spacing */
    text-align: center;
    width: 200px; /* Set a fixed width for each box, adjust as needed */
    border: 2px solid #616464; /* Default border color */
    transition: border-color 0.3s ease; /* Smooth transition on border color change */
  }
  
  .key-feature-box:hover {
    border-color: #fff; /* Change border color on hover */
    box-shadow: 0 0 10px rgba(0, 123, 255, 0.8); /* Add a subtle box shadow on hover */
  }
  
  .key-feature-box h4 {
    font-size: 1.2em;
    margin-bottom: 10px;
    color: #fff; /* Set text color */
  }
  
  .key-feature-box p {
    background-color: #656073;
    font-size: 1em;
    color: white;
  }
  
 
.pricing-section {
    display: flex;
    background-color: #a8b1b0;
    justify-content: space-around;
    text-align: center;
    margin: 40px 0;
    padding: 20px;
  }
  
  .pricing-intro {
    flex: 0 2 70%;
    font-size: 1.7em;
    color: rgb(61, 59, 59);
  }

 
  .pricing-box {
    flex: 0 1 30%; /* Adjust the width as needed */
    background-color: #619b88;
    border: 1px solid #ddd;
    margin: 20px;
    padding: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .pricing-box h3 {
    font-size: 1.5em;
  }
  
  .price {
    font-size: 1.2em;
    margin: 10px 0;
  }
  
  .pricing-box ul {
    list-style-type: none;
    padding: 0;
  }
  
  .pricing-box ul li {
    margin-bottom: 10px;
  }
  
  .cta-button {
    display: inline-block;
    background-color: #007bff;
    color: white;
    padding: 10px 20px;
    text-decoration: none;
    margin-top: 10px;
  }
  
  

  .HowItWork-section {
    text-align: center;
    margin: 40px 0;
    background-color: #E6EBE0; /* Light Pink Background */
    padding: 20px;
  }
  
  .HowItWork-section h3 {
    font-size: 2.5em;
    margin-bottom: 10px;
  }
  
  .cta-section {
    text-align: center;
    margin: 40px 0;
    background-color: #E6EBE0; /* Light Pink Background */
    padding: 20px;
  }
  
  .cta-section h2 {
    font-size: 2.5em;
    margin-bottom: 10px;
  }
  
  .cta-section button {
    background-color: #51865d;
    color: white;
    padding: 5px 8px;
    font-size: 1.0em;
    border: none;
    cursor: pointer;
  }
  
  /* src/components/Home/Home.css */

/* ... Existing styles ... */

.app-footer {
    background-color: #4e4d4d;
    color: white;
    text-align: center;
    padding: 20px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .app-footer a {
    color: white;
    text-decoration: none;
    margin: 0 10px;
  }
  
  .copyright-text {
    flex: 1; /* Take remaining space */
    text-align: left;
  }
  
  .social-icons {
    display: flex;
    align-items: center;
  }
  
  .social-icons a {
    color: white;
    text-decoration: none;
    margin: 0 10px;
  }
  
